import { AppBar, Toolbar, Typography } from "@mui/material"

function SubHeader(content:string) {
    return (
        <AppBar position="static" color="secondary">
            <Toolbar>
                <Typography variant="h5" align="center" sx={{
                fontWeight: 'bold'
                }}>
                    {content}
                </Typography>
            </Toolbar>
        </AppBar>
    )
}

export default SubHeader