import { createTheme } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    primary: {
      main: '#556cd6', // メインカラー
      contrastText: '#ffffff'
    },
    secondary: {
      main: '#05f5ed', // サブカラー
      contrastText: '#ffffff'
    },
    error: {
      main: '#red', // エラーカラー
    },
    background: {
        default: '#f4f6f8', // 全体の背景色
        paper: '#ffffff', // カードなどの背景色
    },
  },
  typography: {
    fontFamily: [
      'Noto Sans JP', // 日本語フォント
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
    ].join(','),
    h4: {
        fontWeight: 700,
        fontSize: '2.125rem', // 34px
        lineHeight: 1.235,
        letterSpacing: '0.00735em',
    },
    h5: {
        fontWeight: 500,
        fontSize: '1.5rem', // 24px
        lineHeight: 1.334,
        letterSpacing: '0em',
    }
  },
  // その他のカスタマイズ (spacing, breakpointsなど)
  spacing: 8, // 1単位が8px
  shape: {
      borderRadius: 8, // 全体の角丸
  },
});

export default theme;